<script>
import { gsap } from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';
import MotionPathPlugin from 'gsap/MotionPathPlugin';

export default {
  methods: {
    setAnimate() {
      const trailPumpkin = document.querySelector('.trail-pumpkin');
      const trailPumpkinM = document.querySelector('.trail-pumpkin-mobile');
      ScrollTrigger.create({
        trigger: trailPumpkin,
        markers: false,
        onEnter() {
          gsap.to('#pumpkin', {
            duration: 10,
            ease: 'power1.inOut',
            motionPath: {
              path: '#path',
              align: '#path',
              alignOrigin: [0.5, 0.5],
            },
          });
        },
      });
      ScrollTrigger.create({
        trigger: trailPumpkinM,
        markers: false,
        onEnter() {
          gsap.to('#pumpkin-m', {
            duration: 5,
            ease: 'power1.inOut',
            motionPath: {
              path: '#path-m',
              align: '#path-m',
              alignOrigin: [0.5, 0.5],
            },
          });
        },
      });
    },
    goSignUp() {
      window.open('https://2022.thef2e.com/');
    },
  },
  created() {
    gsap.registerPlugin(ScrollTrigger, MotionPathPlugin);
  },
  mounted() {
    this.setAnimate();
  },
};
</script>

